import React, { Component } from 'react';
/*
 *	Componentes desenvolvidos para o site
 *	-----------------------------------
 */
import STRSZK from './Satorusuzuki';
import Portfolio from './Portfolio';
/*
 *	As informações dos projetos serão inseridas nesse json
 */
import projetos from './Projects.json';
/*
 *	Estilos de componentes
 *	-----------------------------------
 */
import './styles/Master.css';
import './styles/LogoAnimation.css';
import './styles/Portfolio.css';
import './styles/ProjetoModal.css';
/*
 *	 Início do componente master
 *	-----------------------------------
 */
class Master extends Component {
	constructor(props, context) {
		super(props, context);
		this.state = {
			masterState: 'intro',
		};
	}

	componentDidMount() {
		setTimeout(() => {
			this.setState({ masterState: 'home' });
		}, 3000);
	}

	render() {
		return (
			<div id='master-container' className={this.state.masterState}>
				<STRSZK></STRSZK>
				<div id='contact'>
					<p>About</p>
					<p>Work</p>
				</div>

				<Portfolio projetos={projetos}></Portfolio>


			</div>
		);
	}
}

export default Master;
