import React, { Component } from 'react';
import Close from './icons/close.svg'

class ProjetoModal extends Component {
	constructor(props, context) {
		super(props, context);
		this.state = {
			estado: 0,
			projeto: {
				pasta: 'uniqlo',
				titulo: 'Uniqlo x Disney',
				capa: 'cover.png',
				descricao: 'Texto lorem ipsum sit amet',
				copy: '',
				imagens: [
					'satoru_suzuki_google-packaging_memory-box_4_4.jpg',
					'satoru_suzuki_google-packaging_memory-box_4_4.jpg',
				],
			},
		};
	}

	closeModal() {
		document.getElementById('project-modal').classList = 'modal-fechando';
	}

	render() {
		return (
			<>
				<div id='project-modal'>
                    <img className='close-btn' src={Close} alt=""  onClick={() => this.closeModal()}/>
					<div className='project-info'>
						<h2>{this.props.projeto.titulo}</h2>
						<p>{this.props.projeto.descricao}</p>
						<p>{this.props.projeto.copy}</p>
					</div>
					<div id='modal-wrap' className='modal-wrap'>
						{Object.entries(this.props.projeto.imagens).map(
							([chave, value], index) => {
								return (
									<img
										key={chave}
										className={'imagm-' + chave}
										src={
											'./projects/' +
											this.props.projeto.pasta +
											'/media/' +
											value
										}
										alt=''
									/>
								);
							}
						)}
					</div>
				</div>
			</>
		);
	}
}

export default ProjetoModal;
