import React, { Component } from 'react';
import Modal from './ProjetoModal';
/*
 *	 Início do componente do portfolio
 *	-----------------------------------
 */
class Portfolio extends Component {
	constructor(props, context) {
		super(props, context);
		this.state = {
			projeto_over: '',
			tags_over: [],
			projetos: {
				1: {
					pasta: 'nome da pasta',
					titulo: 'Uniqlo x Disney',
					capa: 'cover.png',
					descricao: 'Texto lorem ipsum sit amet',
					copy: '',
					imagens: ['imagem1.png', 'imagem2.png'],
					tags: [],
				},
			},
			modal: {
				estado: 0,
				projeto: {
					pasta: '',
					titulo: 'Uniqlo x Disney',
					capa: 'cover.png',
					descricao: 'Texto lorem ipsum sit amet',
					copy: '',
					imagens: [
						'satoru_suzuki_google-packaging_memory-box_4_4.jpg',
						'satoru_suzuki_google-packaging_memory-box_4_4.jpg',
					],
				},
			},
		};
	}

	componentDidMount() {
		document
			.getElementById('portfolio-component')
			.addEventListener('wheel', (event) => {
				var scrollProject = document.getElementById('projects-list');
				var scrollAmout = 100 * Math.sign(event.deltaY);

				setTimeout(() => {
					scrollProject.scrollLeft += scrollAmout * 0.01;
				}, 5);
				setTimeout(() => {
					scrollProject.scrollLeft += scrollAmout * 0.02;
				}, 10);
				setTimeout(() => {
					scrollProject.scrollLeft += scrollAmout * 0.03;
				}, 15);
				setTimeout(() => {
					scrollProject.scrollLeft += scrollAmout * 0.04;
				}, 20);
				setTimeout(() => {
					scrollProject.scrollLeft += scrollAmout * 0.05;
				}, 25);
				setTimeout(() => {
					scrollProject.scrollLeft += scrollAmout * 0.06;
				}, 30);
				setTimeout(() => {
					scrollProject.scrollLeft += scrollAmout * 0.07;
				}, 35);
				setTimeout(() => {
					scrollProject.scrollLeft += scrollAmout * 0.08;
				}, 40);
				setTimeout(() => {
					scrollProject.scrollLeft += scrollAmout * 0.09;
				}, 45);
				setTimeout(() => {
					scrollProject.scrollLeft += scrollAmout * 0.1;
				}, 50);
			});
	}

	abrirProjeto(projeto) {
		document.getElementById('project-modal').classList = 'modal-ativo';
		var modalScroll = document.getElementById('modal-wrap');
		modalScroll.scrollTop = 0;

		if (this.state.modal.projeto.pasta !== projeto.pasta) {
			this.setState({
				modal: { projeto: projeto },
			});
		}
	}

	setOver(projeto, tags) {
		if (this.state.projeto_over !== projeto) {
			this.setState({ projeto_over: projeto, tags_over: tags });
		}
	}

	render() {
		return (
			<>
				
				<div id='project-tittle-highlight'>
					<span>{this.state.projeto_over}</span>
				</div>
				<div id='portfolio-component'>
					<div id='projects-list'>
						{Object.entries(this.props.projetos).map(
							([chave, value], index) => {
								return (
									<div
										className={
											'project-card ' + value.pasta
										}
										onMouseOver={() =>
											this.setOver(
												value.titulo,
												value.tags
											)
										}
										onMouseLeave={() =>
											this.setOver('', [])
										}
										key={chave}
										data-chave={chave}
										onClick={() => this.abrirProjeto(value)}
									>	
									
									<div className="overlay-filter"></div>
										<h2>
											{value.titulo}
										</h2>
										{/* <span>{value.pasta}</span>
										<span>{value.titulo}</span> */}

										<img
											className='project-thumbnail'
											src={
												'./projects/' +
												value.pasta +
												'/media/' +
												value.capa
											}
											alt=''
										/>
									</div>
								);
							}
						)}
					</div>
				</div>
				<Modal projeto={this.state.modal.projeto}></Modal>
				<nav>
					<ul>
						<li
							className={
								this.state.tags_over.includes('product')
									? 'ativo'
									: 'inativo'
							}
						>
							Product Design
						</li>
						<li
							className={
								this.state.tags_over.includes('graphic')
									? 'ativo'
									: 'inativo'
							}
						>
							Graphic
						</li>
						<li
							className={
								this.state.tags_over.includes('packaging')
									? 'ativo'
									: 'inativo'
							}
						>
							Packaging
						</li>
						<li
							className={
								this.state.tags_over.includes('3d')
									? 'ativo'
									: 'inativo'
							}
						>
							3d Modeling
						</li>
						<li
							className={
								this.state.tags_over.includes('uxui')
									? 'ativo'
									: 'inativo'
							}
						>
							UX/UI Design
						</li>
					</ul>
					<ul className='only-mobile'>
						<li>Product Design</li>
						<li>Graphic</li>
						<li>Packaging</li>
						<li>3d Modeling</li>
						<li>UX/UI Design</li>
					</ul>
					<ul className='only-mobile'>
						<li>Product Design</li>
						<li>Graphic</li>
						<li>Packaging</li>
						<li>3d Modeling</li>
						<li>UX/UI Design</li>
					</ul>
					<ul className='only-mobile'>
						<li>Product Design</li>
						<li>Graphic</li>
						<li>Packaging</li>
						<li>3d Modeling</li>
						<li>UX/UI Design</li>
					</ul>
				</nav>
			</>
		);
	}
}

export default Portfolio;
